.layer-bar_chart_h {
    --cscla: #EB3B7F;
    --csclA: #CF477D;
    --csclb: #FFD100;
    --csclB: #E8B228;
    --csclc: #00CDFF;
    --csclC: #41B5D9;
    --cscld: #55E16D;
    --csclD: #60BA6F;
    --csclF: #2D3E56;
    --csclG: #666666;
}

.layer-bar_chart_h .clA { color: var(--csclA); }
.layer-bar_chart_h .clF { color: var(--csclF); }
.layer-bar_chart_h .def { background: var(--csclA); }
.layer-bar_chart_h .div { background: var(--csclB); }
.layer-bar_chart_h .par { background: var(--csclC); }
.layer-bar_chart_h .att { background: var(--csclD); }

.layer-bar_chart_h {
    position: absolute;
    right: 4%;
    bottom: 8%;
    width: 35%;
    height: 30%;
    padding: 2%;
    border-radius: 5px;
}

.layer-bar_chart_h .bars {
    width: 100%;
    height: 100%;
    background: #dde4ec;
}

.layer-bar_chart_h .bars .bar {
    height: 25%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
}

.layer-bar_chart_h .bars .bar i.fa {
    width: 10%;
    opacity: 1;
    font-size: 1em;
    margin-right: 10px;
}
.layer-content.no-width .layer-bar_chart_h .bars .bar i.fa {
    opacity: 0;
}
.layer-bar_chart_h .bars .bar .line {
    width: 80%;
    height: 20%;
    border-radius: 5px;
    background: #cccccc;
}

.layer-bar_chart_h .bars .bar .line .fill {
    width: 0%;
    height: 100%;
    border-radius: 5px;
    transition: all 1.5s ease-in-out;
}

#codescrum-hero .overlay .cs-layers-img .layer.layer-1:hover .layer-bar_chart_h .bars .bar .line .fill.exp-20 { width: 20%; }
#codescrum-hero .overlay .cs-layers-img .layer.layer-1:hover .layer-bar_chart_h .bars .bar .line .fill.exp-40 { width: 40%; }
#codescrum-hero .overlay .cs-layers-img .layer.layer-1:hover .layer-bar_chart_h .bars .bar .line .fill.exp-70 { width: 70%; }
#codescrum-hero .overlay .cs-layers-img .layer.layer-1:hover .layer-bar_chart_h .bars .bar .line .fill.exp-80 { width: 80%; }

@keyframes left0to100 {
    from { left: 0%; }
    to { left: 100%; }
}
@keyframes left-100to0 {
    from { left: -100%; }
    to { left: 0%; }
}