/*****************************************/
/* Typewriter Style                      */
/*****************************************/

/* colors of monokai: http://www.colourlovers.com/palette/1718713/Monokai
    orchid(pink): #F92672
    henn1nk (green): #A6E22E
    pumpkin spice (orange): #FD971F 
    bounded rationality (blue): #66D9EF
*/

.typewriter-container {
    --deff: #6164db;
    --attr: #00CDFF;
    --orch: #EB3B7F;
    --beta: #41B5D9;
    --henn: #55E16D;
    --spic: #FD971F;
    --clas: #FFD100;
    --parc: #F8F8F2;
    --comm: #60BA6F;
}

.layer-typewriter .def { background: var(--deff); }
.layer-typewriter .div { background: var(--orch); }
.layer-typewriter .par { background: var(--parc); }
.layer-typewriter .attr { background: var(--attr); }
.layer-typewriter .class { background: var(--clas); }
.layer-typewriter .comment { background: var(--comm) }
.layer-typewriter .class-name { background: var(--henn); }

.layer-typewriter .group {
    position: relative;
    height: 100%;
}

.layer-typewriter .line {
    height: 5%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.layer-typewriter .line2 { margin-left: 5%; }
.layer-typewriter .line3 { margin-left: 10%; }
.layer-typewriter .line4 { margin-left: 15%; }
.layer-typewriter .line5 { margin-left: 20%; }

.layer-typewriter .dash {
    height: 40%;
    margin-right: 5px;
    border-radius: 10px;
    transition: all 0.5s ease-in-out;
    box-shadow: -1px 1px 0px 0px #333;
}

.layer-typewriter .exp-40 { width: 10%; } /* 40px */
.layer-typewriter .exp-70 { width: 15%; } /* 70px */
.layer-typewriter .exp-90 { width: 20%; } /* 90px */
.layer-typewriter .exp-110 { width: 25%; } /* 110px */
.layer-typewriter .exp-200 { width: 50%; } /* 200px */
.layer-typewriter .exp-300 { width: 75%; } /* 300px */

.layer-typewriter.no-width .exp-40 { width: 0%; } /* 40px */
.layer-typewriter.no-width .exp-70 { width: 0%; } /* 70px */
.layer-typewriter.no-width .exp-90 { width: 0%; } /* 90px */
.layer-typewriter.no-width .exp-110 { width: 0%; } /* 110px */
.layer-typewriter.no-width .exp-200 { width: 0%; } /* 200px */
.layer-typewriter.no-width .exp-300 { width: 0%; } /* 300px */

.layer-typewriter.is-animated .exp-40 { animation: expand-40 0.5s linear forwards; }
.layer-typewriter.is-animated .exp-70 { animation: expand-70 0.5s linear forwards; }
.layer-typewriter.is-animated .exp-90 { animation: expand-90 0.5s linear forwards; }
.layer-typewriter.is-animated .exp-110 { animation: expand-110 0.5s linear forwards; }
.layer-typewriter.is-animated .exp-200 { animation: expand-200 0.5s linear forwards; }
.layer-typewriter.is-animated .exp-300 { animation: expand-300 0.5s linear forwards; }

.layer-typewriter.is-animated .group-1 {
    animation: writerScroll 2s linear 1;
    animation-delay: 2s;
    position: relative;
}

.layer-typewriter.is-animated .group-2 {
    animation: writerScroll 2s linear 1;
    animation-delay: 2s;
    position: relative;
}

.layer-typewriter.is-animated .group-1 .line:nth-child(01) .dash { animation-delay: 0.1s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(02) .dash { animation-delay: 0.2s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(03) .dash { animation-delay: 0.3s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(04) .dash { animation-delay: 0.4s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(05) .dash { animation-delay: 0.5s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(06) .dash { animation-delay: 0.6s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(07) .dash { animation-delay: 0.7s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(08) .dash { animation-delay: 0.8s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(09) .dash { animation-delay: 0.9s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(10) .dash { animation-delay: 1.0s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(11) .dash { animation-delay: 1.1s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(12) .dash { animation-delay: 1.2s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(13) .dash { animation-delay: 1.3s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(14) .dash { animation-delay: 1.4s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(15) .dash { animation-delay: 1.5s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(16) .dash { animation-delay: 1.6s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(17) .dash { animation-delay: 1.7s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(18) .dash { animation-delay: 1.8s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(19) .dash { animation-delay: 1.9s; }
.layer-typewriter.is-animated .group-1 .line:nth-child(20) .dash { animation-delay: 2.0s; }

.layer-typewriter.is-animated .group-2 .line:nth-child(01) .dash { animation-delay: 2.1s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(02) .dash { animation-delay: 2.2s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(03) .dash { animation-delay: 2.3s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(04) .dash { animation-delay: 2.4s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(05) .dash { animation-delay: 2.5s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(06) .dash { animation-delay: 2.6s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(07) .dash { animation-delay: 2.7s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(08) .dash { animation-delay: 2.8s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(09) .dash { animation-delay: 2.9s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(10) .dash { animation-delay: 3.0s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(11) .dash { animation-delay: 3.0s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(12) .dash { animation-delay: 3.1s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(13) .dash { animation-delay: 3.1s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(14) .dash { animation-delay: 3.2s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(15) .dash { animation-delay: 3.2s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(16) .dash { animation-delay: 3.3s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(17) .dash { animation-delay: 3.3s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(18) .dash { animation-delay: 3.4s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(19) .dash { animation-delay: 3.4s; }
.layer-typewriter.is-animated .group-2 .line:nth-child(20) .dash { animation-delay: 3.5s; }

@keyframes expand-40 {
    from { width: 0%; }
    to { width: 10%; }
}

@keyframes expand-70 {
    from { width: 0%; }
    to { width: 15%; }
}

@keyframes expand-90 {
    from { width: 0%; }
    to { width: 20%; }
}

@keyframes expand-110 {
    from { width: 0%; }
    to { width: 25%; }
}

@keyframes expand-200 {
    from { width: 0%; }
    to { width: 50%; }
}

@keyframes expand-300 {
    from { width: 0%; }
    to { width: 75%; }
}

@keyframes writerScroll {
    from { top: 0%; }
    to { top: -100%; }
}
@keyframes opactity0to1 {
    from { opacity: 0; }
    to { opacity: 1; }
}