.moving_dots-container {
    --deff: #dee5ed;
    --blNa: #2D3E56;
    --liYe: #FFD100;
    --daYe: #E8B228;
    --liGr: #55E16D;
    --daGr: #60BA6F;
    --liBl: #00CDFF;
    --daBl: #41B5D9;
    --liRe: #EB3B7F;
    --daRe: #CF477D;
    --shDw: #767676;
    --ddDD: #DDDDDD;
}

.layer-moving_dots .def {
    background: var(--deff);
}

.layer-moving_dots .blNa {
    background: var(--blNa);
}

.layer-moving_dots .daGr {
    background: var(--daGr);
}

.layer-moving_dots .daYe {
    background: var(--daYe);
}

.layer-moving_dots .daBl {
    background: var(--daBl);
}

.layer-moving_dots .daRe {
    background: var(--daRe);
}

.layer-moving_dots {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 95%;
    height: 95%;
    border-radius: 10px;
    transform: translate(-50%, -50%);
}

.layer-moving_dots .exp-25 {
    width: 25%;
}

.layer-moving_dots .exp-50 {
    width: 50%;
}

.layer-moving_dots .exp-70 {
    width: 70%;
}

.layer-moving_dots.no-width .exp-25,
.layer-moving_dots.no-width .exp-50,
.layer-moving_dots.no-width .exp-70 {
    width: 0%;
}

.layer-moving_dots .moving_dots-container {
    width: 100%;
    height: 100%;
    background: transparent;
}

.layer-moving_dots .banner-wrapper {
    position: absolute;
    top: 13%;
    left: 10%;
    width: 19.5%;
    height: 67.5%;
    border-radius: 5px;
    border: 1px solid var(--ddDD);
    box-shadow: 0px 1px 1px 0px var(--shDw);
}

.layer-moving_dots .hexagon-wrapper {
    position: absolute;
    top: 44.5%;
    left: 50.8%;
    width: 33%;
    height: 35%;
    margin: 3.5%;
    text-align: center;
    display: inline-block;
    transform: translate(-50%, -50%);
    z-index: 3;
}

.layer-moving_dots .hexagon {
    height: 100%;
    width: calc(100% * 0.57735);
    display: inline-block;
}

.layer-moving_dots .hexagon:after,
.layer-moving_dots .hexagon:before {
    position: absolute;
    content: '';
    top: 0;
    right: calc((100% / 2) - ((100% * 0.57735) / 2));
    width: inherit;
    height: inherit;
    background-color: inherit;
    transform: rotateZ(60deg);
}

.layer-moving_dots .hexagon:before {
    transform: rotateZ(60deg);
}

.layer-moving_dots .hexagon:after {
    transform: rotateZ(-60deg);
}

.layer-moving_dots .dot {
    position: absolute;
    width: 4%;
    height: 4%;
    z-index: 4;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.5s ease;
    opacity: 1;
}

.layer-moving_dots.no-width .banner-wrapper .dot {
    background: var(--shDw);
}

.layer-moving_dots .banner-wrapper .dot {
    left: 0%;
    width: 37%;
    height: 11%;
}

