/* Circuit Dots */
.layer-moving_dots.is-animated .dot_1 {
    animation: bigCircuitFull_1 3s linear forwards 0.0s, blinkYellow 0.2s 30;
}

.layer-moving_dots.is-animated .dot_2 {
